<template>
  <div id="register" class="flex-center flex-column" style="width:100%;height:100%;">
      <div class="flex-center" style="width:90%;height:150px;margin-bottom:24px;">
        <img src="../../assets/img/icon/logo@3x.png" alt="" style="max-height:100%;"/>
      </div>
      <van-form @submit="_on_register">
        <van-field
            v-model="userName"
            name="用户名"
            label="用户名"
            placeholder="用户名"
            :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
            v-model="userPsw"
            type="password"
            name="密码"
            label="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-field
            v-model="userPsw2"
            type="password"
            name="重复密码"
            label="重复密码"
            placeholder="重复密码"
            :rules="[{ required: true, message: '请再次填写密码' }]"
        />
        <van-field
            v-model="userMobile"
            name="手机号"
            label="手机号"
            placeholder="手机号"
            :rules="[{ required: true, message: '请填写手机号' }]"
        />
        <van-field
            v-model="parentCode"
            name="推荐人码"
            label="推荐人码"
            placeholder="推荐人码"
            :rules="[{ required: true, message: '请填写推荐人码' }]"
        />
        <div style="margin: 16px;">
            <van-button round block native-type="submit">注册</van-button>
        </div>
        <div style="margin: 16px;">
            <router-link to="/myLogin" style="color:#fff;font-size:12px;">已有账号</router-link>
        </div>
    </van-form>
  </div>
</template>

<script>
import { register, getPersonalInfo } from '@/api';
export default {
    data: ()=>{
        return {
            userName: '',
            userPsw: '',
            userPsw2: '',
            userMobile: '',
            parentCode: ''
        }
    },
    created() {
        this.checkLogin();
    },
    mounted(){
        document.title = '登录';
    },
    methods: {
        /**
         * 检查登录状态
         * 如果登录状态有效，则返回上一个页面
         */
        checkLogin(){
            let token = sessionStorage.getItem('token') || localStorage.getItem('token');
            if(token){
                sessionStorage.setItem('token',token);
                getPersonalInfo().then(res=>{
                if (res.code == 0) {
                    sessionStorage.setItem("userId", res.data.userId);
                    sessionStorage.setItem("userShop", res.data.userShop);
                    sessionStorage.setItem("shopCode", res.data.shopCode);
                    sessionStorage.setItem(
                    "headimgurl",
                    this.$utils.dealImg(res.data.userHeader)
                    );
                    sessionStorage.setItem("userName", res.data.userName);
                    sessionStorage.setItem("userName2", res.data.userName2 || "");
                    sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");

                    this.$router.push('/');
                }
                })
                
            }
        },
        _on_register(){
            let { userName, userMobile, userPsw, userPsw2, parentCode } = this;
            register({
                userName,
                userMobile,
                userPsw,
                userPsw2,
                parentCode
            }).then(res=>{
                if(res.code === 0){
                    sessionStorage.setItem("userId", res.data.userId);
                    sessionStorage.setItem("userShop", res.data.userShop);
                    sessionStorage.setItem("shopCode", res.data.shopCode);
                    sessionStorage.setItem(
                    "headimgurl",
                    this.$utils.dealImg(res.data.userHeader)
                    );
                    sessionStorage.setItem("userName", res.data.userName);
                    sessionStorage.setItem("userName2", res.data.userName2 || "");
                    sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");
                    this.$router.push('/');
                    this.$toast.success(res.msg);
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        }
    }
}
</script>

<style lang="scss">
    .flex-center {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .flex-column {
        display: flex;
        flex-direction: column;
    }

    #register {

        .van-cell {
            background-color: initial;
        }
        .van-form {
            min-width: 90%;
        }
        .van-form .van-field * {
            color: #fff !important;
        }
    }
    
</style>